.inputsSection {
    margin-top: 10px;
    display: flex;
  }
  
  .inputSection {
    background-color: white;
    border: 0;
    border-radius: 8px;
    border-top-right-radius: 0px;
    border-bottom-right-radius: 0px;
    font-size: 18px;
    padding: 15px;
    height: 40px;
    width: 300px;
    
  }
  
  .searchIcon {
    height: 70px;
    width: 50px;
    background-color: rgb(255, 255, 255);
    display: grid;
    place-items: center;
    border-radius: 8px;
    border-top-left-radius: 0px;
    border-bottom-left-radius: 0px;
   }
  
  input:focus {
    outline: none;
  }
  .searchIcon svg {
    font-size: 35px;
    
  }
  
  .dataResult {
    margin-top: 5px;
    width: 300px;
    height: 200px;
    background-color: white;
    box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
    overflow: hidden;
    overflow-y: auto;
  }
  
  .dataResult::-webkit-scrollbar {
    display: none;
  }
  
  .dataResult .dataItem {
    width: 100%;
    height: 50px;
    display: flex;
    align-items: center;
    color: black;
  }
  
  .dataItem p {
    margin-left: 10px;
  }
  .dataItem {
    text-decoration: none;
  }
  
  .dataItem:hover {
    background-color: lightgrey;
  }
  
  .clearBtn {
    cursor: pointer;
  }

